import React, { Component } from "react";
import "./Greeting.css";

export default class Greeting extends Component {
  state = {
    greeting: ["JavaScript", "ReactJS", "React Native", "NodeJS"],
    selected: 0,
  };

  intervalFunc = () => {
    const { greeting, selected } = this.state;
    if (this.timer === 2) {
      this.timer = 1;
      if (selected < greeting.length - 1) {
        this.setState((state) => ({
          selected: state.selected + 1,
        }));
      } else {
        this.setState({ selected: 0 });
      }
    } else {
      this.timer += 1;
    }
  };

  componentDidMount = () => {
    this.timer = 1;
    this.typingTimer = setInterval(this.intervalFunc, 1000);
  };

  componentWillUnmount = () => {
    clearInterval(this.typingTimer);
  };

  render() {
    const { greeting, selected } = this.state;
    return (
      <div className="container greeting">
        <div className="titleContainer">
          <span className="helloTitle">HELLO!</span>
          <span className="greetingTitle">
            I AM
            <span className="d-flex justify-content-center">
              &#123;
              {greeting.map(
                (item, index) =>
                  index === selected && (
                    <p
                      key={index}
                      className="typingText"
                      style={{
                        animation: `typing 2s steps(${item.length}) infinite`,
                      }}
                    >
                      {item}
                    </p>
                  )
              )}
              &#125;
            </span>
            DEVELOPER
            <span className="sloganTitle">INTERACTION IS STRENGTH</span>
          </span>
        </div>
      </div>
    );
  }
}
