import React, { Component } from "react";
import "./Header.css";

const ANIMATION_DURATION = 1200;

export default class Header extends Component {
  scroll = (target, duration) => {
    const element = document.querySelector(target);
    const elementRect = element.getBoundingClientRect();
    const elementPosition = elementRect.top;
    const startPosition = window.pageYOffset;
    const distance = elementPosition;
    let startTime = null;
    console.log(distance);

    const ease = (t, b, c, d) => {
      t /= d / 2;
      if (t < 1) return (c / 2) * Math.pow(2, 10 * (t - 1)) + b;
      t--;
      return (c / 2) * (-Math.pow(2, -10 * t) + 2) + b;
    };

    const animation = (currentTime) => {
      if (!startTime) startTime = currentTime;
      const timeElapsed = currentTime - startTime;
      const run = ease(timeElapsed, startPosition, distance, duration);
      window.scrollTo(0, run);
      if (timeElapsed < duration) window.requestAnimationFrame(animation);
    };
    if (Math.abs(distance) > 20) {
      window.requestAnimationFrame(animation);
    }
  };

  render() {
    return (
      <nav className="navbar navbar-expand-sm header">
        <div className="container">
          <span
            className="navbar-brand headerItem"
            onClick={() => this.scroll("#greetingSection", ANIMATION_DURATION)}
          >
            ALVIN
          </span>
          <ul className="navbar-nav col-sm-7 offset-sm-5 nav-list">
            <li className="nav-item">
              <span
                className="nav-link headerItem"
                onClick={() => this.scroll("#aboutSection", ANIMATION_DURATION)}
              >
                About
              </span>
            </li>
            <li className="nav-item">
              <span className="nav-link headerItem">My Projects</span>
            </li>
            <li className="nav-item">
              <span className="nav-link headerItem">Skills</span>
            </li>
            <li className="nav-item">
              <span className="nav-link headerItem">My Blogs</span>
            </li>
            <li className="nav-item">
              <span className="nav-link headerItem">Contact</span>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}
