import React, { Component } from "react";
import PropTypes from "prop-types";
import "./Button.css";

export default class Button extends Component {
  PropTypes = {
    text: PropTypes.string,
    style: PropTypes.object,
    dark: PropTypes.bool,
  };
  constructor(props) {
    super(props);
    this.state = {
      defaultStyle: this.getTheme(props.dark),
    };
  }

  getTheme = (dark) => {
    let defaultStyle = {
      backgroundColor: "#FFFFFF",
      color: "#000000",
    };
    if (dark) {
      defaultStyle = {
        backgroundColor: "#000000",
        color: "#FFFFFF",
      };
    }
    return defaultStyle;
  };

  toggleTheme = (dark) => {
    this.setState({ defaultStyle: this.getTheme(dark) });
  };

  render() {
    const { text, style, dark } = this.props;
    const { defaultStyle } = this.state;
    return (
      <span
        onMouseOver={() => {
          this.toggleTheme(!dark);
        }}
        onMouseLeave={() => {
          this.toggleTheme(dark);
        }}
        className="btn"
        style={{ ...defaultStyle, ...style }}
      >
        {text}
      </span>
    );
  }
}
